import http from '../../http-common'

class TaxGroupSercice {
  getAll () {
    return http.get('TaxGroup')
  }

  getAllPagnation (params) {
    return http.get('TaxGroup', { params })
  }

  get (id) {
    return http.get(`TaxGroup/getById?Id=${id}`)
  }

  create (data) {
    return http.post('TaxGroup', data)
  }

  update (id, data) {
    return http.put(`TaxGroup?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`TaxGroup?Id=${id}`)
  }
}

export default new TaxGroupSercice()
