<template>
  <va-card :title="$t('tables.labelTaxGroup')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        <va-icon name="fa fa-circle" :color="props.rowData.color" size="8px" />
      </template>
      <template slot="valueHaveTax" slot-scope="props">
        {{props.rowData.valueHaveTax  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>
    <va-modal v-model="editModal" no-outside-dismiss
      size="large"
      :title=" $t('generic.editItemTitle') "
      hide-default-actions >
      <form @submit.prevent="okEditfun()">
        <div class="row">
          <div class="flex md4 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.name')}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md4 sm12 xs12">
            <label class="input-label"> {{$t('placeholder.value')}} </label>
            <va-input v-model="currentobject.value" type="number" step="0.1"
              :error="numircriquierdvalidation(currentobject.value)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.value')" required/>
          </div>
          <div class="flex md4 sm12 xs12">

            <va-checkbox
              :label="$t('placeholder.valueHaveTax')"
              v-model="currentobject.valueHaveTax"
            />
          </div>
        </div>

        <div class="d-flex justify--center mt-3">
          <va-button  color="gray" flat @click="cancleEditfun()">
            {{ $t('modal.cancel') }}        </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.edit') }}</va-button>
        </div>
      </form>
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title=" $t('generic.creatItemTitle') "
      hide-default-actions >
      <div class="row">
        <form @submit.prevent="okCreatefun()">
          <div class="flex md12 sm12 xs12">
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
            <va-input v-model="currentobject.value" type="number" step="0.1"
              :error="numircriquierdvalidation(currentobject.value)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.value')" required/>
            <va-checkbox
              :label="$t('placeholder.valueHaveTax')"
              v-model="currentobject.valueHaveTax"
            />
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button  color="gray" flat @click="cancleCreatefun()">
              {{ $t('modal.cancel') }}        </va-button>
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.create') }}</va-button>
          </div>
        </form>

      </div>

    </va-modal>
  </va-card>

</template>

<script>
import TaxGroupService from '../../../../services/BasicData/TaxGroupService'
import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      stopBtn: false,
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
      },
      {
        name: 'value',
        title: this.$t('tables.headings.value'),
      },
      {
        name: '__slot:valueHaveTax',
        title: this.$t('tables.headings.valueHaveTax'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api Padination */
    readItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      TaxGroupService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },

    create () {
      this.currentobject.value = parseFloat(this.currentobject.value)

      TaxGroupService.create(this.currentobject).then(response => {
        this.dataTable.push(response.data)
        if (response.headers['x-status'] === 'true') {
          this.loading = false
          this.currentobject = {}
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    edit (user) {
      this.currentobject.value = parseFloat(this.currentobject.value)

      TaxGroupService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === this.currentobject.id)
          this.dataTable[idx].name = this.currentobject.name
          this.currentobject = {}
          this.loading = false
          this.editModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    remove (id) {
      TaxGroupService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.loading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.editModal = false
      this.loading = false
      console.log('ddd')
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false

      this.loading = false
      this.currentobject = {}
    },
    /* end create model */

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
